import * as moment from 'moment';

import { Venue } from './VenueData';
import { Team } from './TeamData';
import { RAW_DATA } from './RawData';
import {UI_URL_DATE_FORMAT} from '../Utils/DateUtil';

export const BBL_DURATION_MINS = 205;

export interface Match {
    matchNumber: number;
    date: Date;
    ground: Venue;
    homeTeam: Team;
    awayTeam: Team;
    ladies: boolean;
    url: string;
}

export const MATCHES: Match[] = [];

const generateDateFromStrings = (date: string, time: string, ground: Venue): Date => {
    const timeZone = ground.state.timeZone;
    const timeZoneSplit: string[] = timeZone.toString().split('.');
    const timeZoneLeading: string = `00${timeZoneSplit[0]}`.substr(-2);
    let timeZoneTrailing: string;
    if (!!timeZoneSplit[1]) {
        timeZoneTrailing = ((Number(timeZoneSplit[1]) / 10) * 60).toString();
    } else {
        timeZoneTrailing = `00`;
    }
    // const remainder = (timeZone % Number(timeZoneSplit[0]));
    const dateString: string = `${date} ${time}:00+${timeZoneLeading}${timeZoneTrailing}`;
    return moment(dateString).toDate();
};

const generateMatchUrl = (matchNo: number, date: string, homeTeam: Team, awayTeam: Team) => {
    const dateMoment = moment(date);
    return `${homeTeam.shortName.toLowerCase()}-v-${awayTeam.shortName.toLowerCase()}-${dateMoment.format(UI_URL_DATE_FORMAT).toLowerCase()}-${matchNo}`;
};

const generateMatchesData = (matchNo: number, date: string, time: string, homeTeam: Team, awayTeam: Team, ground: Venue): Match => {
    const match: Match = {
        matchNumber: matchNo,
        date: generateDateFromStrings(date, time, ground),
        ground,
        homeTeam,
        awayTeam,
        ladies: false,
        url: generateMatchUrl(matchNo, date, homeTeam, awayTeam)
    };
    return match;
};

RAW_DATA.forEach(match => {
    MATCHES.push(generateMatchesData(match[0] as number, match[1] as string, match[2] as string, match[3] as Team, match[4] as Team, match[5] as Venue));
});



