import * as React from 'react';
import * as moment from 'moment';

import './Body.css';

import {timeNow} from '../../Utils/TimeUtil';

import Nav from '../../Components/Nav/Nav';
import {Match} from '../../Data/Matches';
import {matchData} from '../../Data/Data';
import Feature from '../Feature/Feature';
import Footer from '../Footer/Footer';
import {BBL_DURATION_MINS} from '../../Data/Matches';

const AppData = {
    // tslint:disable-next-line
    title: 'When is Big Bash cricket on?',
    description: 'Countdown to next game'
};

export interface BodyProps {
    onToggleNav: () => void;
    navOpen: boolean;
}

interface BodyState {
    matches : Match[];
    selectedMatch : Match;
    currentTime: moment.Moment;
}

export default class Body extends React.PureComponent<BodyProps, BodyState> {
    constructor(props: BodyProps) {
        super(props);
        this.state = {
            matches: matchData,
            selectedMatch: this.initialSelectMatch(),
            currentTime: timeNow()
        };
        this.selectMatch = this.selectMatch.bind(this);
    }

    render() {
        const selectedMatch = this.state.selectedMatch;
        const matches = this.state.matches;
        const navOpen = this.props.navOpen;
        const backgroundImage: React.CSSProperties = {
            backgroundImage: `url("${selectedMatch.ground.imageUrl}")`,
        };

        return (
            <div className="body">
                <div className="body__content" style={backgroundImage}>
                    <Nav
                        onToggleNav={this.props.onToggleNav}
                        navOpen={navOpen}
                        fixtures={matches}
                        onSelectFixture={this.selectMatch}
                        selectedFixture={this.state.selectedMatch}
                        title={AppData.title}
                        description={AppData.description}
                    />
                    <Feature match={selectedMatch} />
                    <Footer />
                </div>
            </div>
        );
    }

    private initialSelectMatch() {
        const currentTime = timeNow();
        const upcomingMatches = matchData.filter(match => {
            return moment(currentTime)
                .subtract(BBL_DURATION_MINS, 'minutes')
                .isBefore(match.date);
        });
        return upcomingMatches[0];
    }

    private selectMatch(match: Match) {
        this.setState({
            selectedMatch: match
        });
        this.props.onToggleNav();
    }
}


