import * as React from 'react';
import * as moment from 'moment';
import * as classNames from 'classnames';
import {
    Link,
    // Redirect,
} from 'react-router-dom';

import './NavMenu.css';

import JSXUtil from '../../Utils/JSXUtil';
import {
    Match,
    BBL_DURATION_MINS
} from '../../Data/Matches';
import {UI_MENU_DATE_FORMAT} from '../../Utils/DateUtil';
import {timeNow} from '../../Utils/TimeUtil';

export interface NavMenuProps {
    onSelectFixture: (fixture: Match) => void;
    selectedFixture: Match;
    fixtures: Match[];
}

interface NavMenuState {
    completeActive: boolean;
    upcomingActive: boolean;
}

export default class NavMenu extends React.PureComponent<NavMenuProps, NavMenuState> {
    constructor(props: NavMenuProps) {
        super(props);
        this.state = {
            completeActive: false,
            upcomingActive: true
        };
        this.toggleSubMenu = this.toggleSubMenu.bind(this);
    }

    public render() {
        const completedMatches = this.props.fixtures.filter((fixture) => {
            return moment(fixture.date).add(BBL_DURATION_MINS, 'minutes').isBefore(timeNow());
        });
        const upcomingMatches = this.props.fixtures.filter((fixture) => {
            return moment(fixture.date).add(BBL_DURATION_MINS, 'minutes').isAfter(timeNow());
        });
        return (
            <div>
                <li className={JSXUtil.conditionalActiveClass('nav__item', this.state.completeActive)} onClick={() => this.toggleSubMenu(true)}>
                    <div className="nav__item_container">
                        <div className="nav__item_number">
                            <i className={JSXUtil.conditionalActiveClass('material-icons', !this.state.completeActive)}>keyboard_arrow_down</i>
                        </div>
                        <div className="nav__item_details">
                            Complete
                        </div>
                    </div>
                </li>
                {this.renderMatches(completedMatches, true)}
                <li className={JSXUtil.conditionalActiveClass('nav__item', this.state.upcomingActive)} onClick={() => this.toggleSubMenu(false)}>
                    <div className="nav__item_container">
                        <div className="nav__item_number">
                            <i className={JSXUtil.conditionalActiveClass('material-icons', !this.state.upcomingActive)}>keyboard_arrow_down</i>
                        </div>
                        <div className="nav__item_details">
                            Upcoming
                        </div>
                    </div>
                </li>
                {this.renderMatches(upcomingMatches, false)}
                {/* <Redirect key={'redirect'} from="/*" to="/"/> */}
            </div>
        );
    }

    private renderIndividualMatch(match: Match, index: number) {
        const time = moment(match.date).utcOffset(match.ground.state.timeZone * 60).local();
        const selected: boolean = match.matchNumber === this.props.selectedFixture.matchNumber;
        const selectedElement = selected ? true : undefined;
        const complete: boolean = time.clone().add(BBL_DURATION_MINS, 'minutes').isBefore(timeNow());
        const listClasses = classNames(
            'nav__item', {
                'nav__item--selected': selected,
                'nav__item--complete': complete
            }
        );
        return (
            <Link
                to={match.url}
                key={index + '-' + match.date.toDateString()}
                className="nav__link"
            >
                <li
                    className={listClasses}
                    onClick={() => this.props.onSelectFixture(match)}
                    data-selected={selectedElement}
                >
                    <div className="nav__item_container">
                        <div className="nav__item_number">
                            {match.matchNumber} :
                        </div>
                        <div className="nav__item_details">
                            <div className="nav__item_teams">
                                {match.homeTeam.shortName} -vs- {match.awayTeam.shortName}
                            </div>
                            <div className="nav__item_venue">
                                {match.ground.name}, {match.ground.suburb}, {match.ground.state.name}
                            </div>
                            <div className="nav__item_date_time">
                                {time.format(UI_MENU_DATE_FORMAT)}
                            </div>
                        </div>
                    </div>
                </li>
            </Link>
        );
    }

    private toggleSubMenu(complete: boolean) {
        if (complete) {
            this.setState({
                completeActive: !this.state.completeActive,
            });
        } else {
            this.setState({
                upcomingActive: !this.state.upcomingActive
            });
        }
    }

    private renderMatches(fixtures: Match[], complete: boolean) {
        const className = classNames(
            'nav__sub_menu',
            {
                'complete': complete,
                'upcoming': !complete,
                'active': (complete && this.state.completeActive) || (!complete && !!this.state.upcomingActive)
            }
        );
        return (
            <ul className={className}>
                {
                    fixtures.map((match, index) => {
                        return this.renderIndividualMatch(match, index);
                    })
                }
            </ul>
        );
    }
}


