import * as React from 'react';
import * as moment from 'moment';

import './CountdownTimer.css';
import {timeNow} from '../../Utils/TimeUtil';
import CountdownUtil from '../../Utils/CountdownUtil';

import CountdownTimerItem from './CountdownTimerItem';
import {BBL_DURATION_MINS} from '../../Data/Matches';

export interface Countdown {
    now: moment.Moment;
    diffInSeconds: number;
    days: number;
    hours: number;
    minutes: number;
    minutesString: string;
    seconds: number;
    secondsString: string;
    inProgress: boolean;
    complete: boolean;
}

interface CountdownTimerProps {
    eventTime: moment.Moment;
    showDays?: boolean;
    showHours?: boolean;
    showMinutes?: boolean;
    showSeconds?: boolean;
}

interface CountdownTimerState {
    countdown: Countdown;
}

export default class CountdownTimer extends React.PureComponent <CountdownTimerProps,
CountdownTimerState> {

    public timer = () => setInterval(() => this.tick(), 1000);

    constructor(props: CountdownTimerProps) {
        super(props);
        this.state = {
            countdown : CountdownUtil.generateCountdown(timeNow(), this.props.eventTime, BBL_DURATION_MINS)
        };
    }

    componentDidMount() {
        this.timer();
    }

    render() {
        const countdownTimer = this.renderCountdown();
        return (
            countdownTimer
        );
    }

    private tick() {
        this.setState({
            countdown : CountdownUtil.generateCountdown(timeNow(), this.props.eventTime, BBL_DURATION_MINS)
        });
    }

    private renderCountdown() {
        const countdown = this.state.countdown;
        if (countdown && countdown.inProgress) {
            return this.renderInProgress();
        } else if (countdown && countdown.complete) {
            return this.renderComplete();
        } else if (countdown) {
            return this.renderCounter(countdown);
        } else {
            return undefined;
        }
    }

    private renderCounter(countdown: Countdown) {
        const daysLabel: string = countdown.days === 1 ? 'day' : 'days';
        const hoursLabel: string = countdown.hours === 1 ? 'hour' : 'hours';
        const minutesLabel: string = countdown.minutes === 1 ? 'minute' : 'minutes';
        return (
            <div className="feature__countdown_timer">
                <CountdownTimerItem value={countdown.days.toString()} label={daysLabel}/>
                <CountdownTimerItem value={countdown.hours.toString()} label={hoursLabel}/>
                <CountdownTimerItem value={countdown.minutesString} label={minutesLabel}/>
                <CountdownTimerItem value={countdown.secondsString} label="seconds"/>
            </div>
        );
    }

    private renderInProgress() {
        return (
            <div className="feature__countdown_timer">
                Game in progress
            </div>
        );
    }

    private renderComplete() {
        return (
            <div className="feature__countdown_timer">
                Game complete
            </div>
        );
    }
}



