import * as React from 'react';

import {
    TEAM,
    Team
} from '../../Data/TeamData';
import {
    VENUE,
    Venue
} from '../../Data/VenueData';

import './PreFetcher.css';

export default class PreFetcher extends React.PureComponent {
    render() {
        return (
            <div className="prefetcher">
                {this.renderTeams()}
                {this.renderVenues()}
            </div>
        );
    }

    private renderTeams() {
        let imageArray: JSX.Element[] = [];
        Object.keys(TEAM).forEach(function(key: KeyType, index: number) {
            const teamKey: Team = TEAM[key];
            imageArray.push(
                <img key={index} src={teamKey.imageUrl} alt=""/>
            );
        });
        return imageArray;
    }

    private renderVenues() {
        let imageArray: JSX.Element[] = [];
        Object.keys(VENUE).forEach(function(key: KeyType, index: number) {
            const venueKey: Venue = VENUE[key];
            imageArray.push(
                <img key={index} src={venueKey.imageUrl} alt=""/>
            );
        });
        return imageArray;
    }

}



