import * as React from 'react';
import * as moment from 'moment';

import CountdownTimer from '../CoundownTimer/CountdownTimer';
import FeatureIntraTeam from './FeatureIntraTeam';

import {Match} from '../../Data/Matches';
import {
    UI_DATE_FORMAT,
    UI_TIME_FORMAT
} from '../../Utils/DateUtil';

import './Feature.css';

export interface FeatureProps {
    match : Match;
}

export default class Feature extends React.PureComponent <FeatureProps> {
    render() {
        const match = this.props.match;
        const time = moment(match.date);

        return (
            <div className="feature">
                <FeatureIntraTeam
                    homeTeam={match.homeTeam}
                    awayTeam={match.awayTeam}
                />
                <div className="feature__match_details">
                    <div className="feature__item">
                        <div className="grey">
                            {`Game ${match.matchNumber}`}
                        </div>
                        <div className="feature__venue_details">
                            {match.ground.name.toString()}, {match.ground.suburb.toString()}, {match.ground.state.name.toString()}
                            <a href={match.ground.mapUrl} target="_blank">
                                <i className="material-icons icon__pin">place</i>
                            </a>
                        </div>
                    </div>
                    <div className="feature__item large">{time.format(UI_DATE_FORMAT)}</div>
                    <div className="feature__item large">{time.format(UI_TIME_FORMAT)}&nbsp;
                        <span className="span grey">(Your time)</span>
                    </div>
                    {this.showLocalTime(time)}
                    <CountdownTimer
                        eventTime={moment(match.date)}
                    />
                    <div className="feature__item">
                        <a href="https://www.bigbash.com.au/ladder" target="_blank">Current standings</a> | <a href="https://en.wikipedia.org/wiki/Big_Bash_League" target="_blank">About BBL</a> | <a href="https://www.bigbash.com.au/tickets">Tickets</a><br/>
                    </div>
                </div>
            </div>
        );
    }

    private showLocalTime(time: moment.Moment) {
        const difference = this.props.match.ground.state.timeZone - ((moment().utcOffset()) / 60);
        return difference !== 0 && (
            <div className="feature__item large">{time.add(difference, 'hours').format(UI_TIME_FORMAT)}&nbsp;
                <span className="span grey">(Local time)</span>
            </div>
        );
    }
}



