export default class UrlUtil {
    public static extractDataFromUrl(pathName: string, index?: number, delimiter?: string) {
        if (!index && !delimiter) {
            return pathName.substring(1);
        }
        if (!index) {
            index = -1;
        }
        if (!delimiter) {
            delimiter = '-';
        }
        if (pathName.substring(0, 1) === '/') {
            pathName = pathName.substring(1); // strip out leading '/'
        }
        const splitUrlArray: string[] = pathName.split(delimiter);
        const data = splitUrlArray.slice(index)[0];
        return data;
    }
}


