import * as React from 'react';

import NavMenu from './NavMenu';
import {
    Match,
} from '../../Data/Matches';

import './Nav.css';

interface NavProps {
    title: string;
    description: string;
    navOpen: boolean;
    fixtures: Match[];
    selectedFixture: Match;
    onToggleNav: () => void;
    onSelectFixture: (match: Match) => void;
}

export default class Nav extends React.PureComponent<NavProps> {
    constructor(props: NavProps) {
        super(props);
        this.toggleNav = this.toggleNav.bind(this);
    }

    render() {
        const sideNavStyles = this.props.navOpen ?
            { transform: 'translateX(0px)' } :
            { transform: 'translateX(-100%)' };

        return (
            <nav className="nav">
                <div className="nav__bar">
                    <div className="nav__toggle" onClick={this.toggleNav}>
                        <i className="nav__toggle_icon material-icons">menu</i>
                    </div>
                    <div className="nav__title_container">
                        <div className="nav__title">
                            <h1>
                                {this.props.title}
                            </h1>
                            <h2>
                                {this.props.description}
                            </h2>
                        </div>
                    </div>
                </div>
                <div style={sideNavStyles} className="nav__menu">
                    <ul>
                        <li className="nav__toggle" onClick={this.toggleNav}>
                            <i className="nav__toggle_icon material-icons">close</i>
                        </li>
                        <NavMenu
                            fixtures={this.props.fixtures}
                            selectedFixture={this.props.selectedFixture}
                            onSelectFixture={this.props.onSelectFixture}
                        />
                    </ul>
                </div>
            </nav>
        );
    }

    private toggleNav() {
        this.props.onToggleNav();
    }
}


