import * as moment from 'moment';
import UrlUtil from './UrlUtil';

export const UI_DATE_FORMAT = 'dddd Do MMM';
export const UI_TIME_FORMAT = 'h:mma';
export const UI_MENU_DATE_FORMAT = 'h:mma, ddd Do MMM YYYY';
export const UI_URL_DATE_FORMAT = 'dddDDMMMYY';

export default class DateUtil {
    public static formatMomentForUrl(dateMoment: moment.Moment): string {
        return dateMoment.format(UI_URL_DATE_FORMAT).toLowerCase();
    }

    public static parseDateFromEndOfUrl(url: string): Date {
        const delimiter = '-';
        const index = -1;
        return moment(UrlUtil.extractDataFromUrl(url, index, delimiter), UI_URL_DATE_FORMAT).toDate();
    }

    public static parseDateFromEndOfUrlAsMoment(url: string): moment.Moment {
        const delimiter = '-';
        const index = -1;
        return moment(UrlUtil.extractDataFromUrl(url, index, delimiter), UI_URL_DATE_FORMAT);
    }
}


