import * as React from 'react';
import {BrowserRouter} from 'react-router-dom';

import './App.css';

import NavOverlay from './Components/Nav/NavOverlay';
import Body from './Components/Body/Body';
import PreFetcher from './Components/PreFetcher/PreFetcher';

interface AppState {
    navOpen: boolean;
    showPrefetcher: boolean;
}
export default class App extends React.PureComponent <{}, AppState> {
    constructor(props: {}) {
        super(props);
        this.state = {
            navOpen: false,
            showPrefetcher: false
        };
        this.toggleNav = this.toggleNav.bind(this);
    }

    componentDidMount() {
        setInterval(
            () => this.setState({
                showPrefetcher: true
            }),
            5000
        );
    }

    render() {
        const state = this.state;
        return (
            <BrowserRouter>
                <div className="app">
                    <Body
                        onToggleNav={this.toggleNav}
                        navOpen={state.navOpen}
                    />
                    <NavOverlay onClick={this.toggleNav} navOpen={state.navOpen}/>
                    {this.buildPrefetcher()}
                </div>
            </BrowserRouter>
        );
    }

    private toggleNav() {
        this.setState({
            navOpen: !this.state.navOpen
        });
    }

    private buildPrefetcher() {
        return !!this.state.showPrefetcher && <PreFetcher />;
    }
}


