import * as classNames from 'classnames';

export default class JSXUtil {
    public static conditionalActiveClass(baseClasses: string, condition: boolean, activeClass?: string): string {
        const active: string = activeClass ? activeClass : 'active';
        return classNames(
            baseClasses,
            {
                [active]: condition
            }
        );
    }
}


