import * as React from 'react';

import {Team} from '../../Data/TeamData';

export interface FeatureIntraTeamProps {
    homeTeam: Team;
    awayTeam: Team;
}

export default class FeatureIntraTeam extends React.PureComponent<FeatureIntraTeamProps> {
    public render() {
        const homeTeam = this.props.homeTeam;
        const awayTeam = this.props.awayTeam;
        return (
            <div className="feature__teams">
                <div className="feature__home_team">
                    <img className="feature__team_logo" src={homeTeam.imageUrl} alt={homeTeam.name}/>
                    <h2 className="feature__team_title">
                        {homeTeam.name}
                    </h2>
                    <a href={homeTeam.officalSite} target="_blank" className="feature__team_website">
                        Offical site <i className="material-icons">open_in_new</i>
                    </a>
                </div>
                <div className="feature__team_spacer"/>
                <div className="feature__away_team">
                    <img className="feature__team_logo" src={awayTeam.imageUrl} alt={awayTeam.name}/>
                    <h2 className="feature__team_title">
                        {awayTeam.name}
                    </h2>
                    <a href={awayTeam.officalSite} target="_blank" className="feature__team_website">
                        Offical site <i className="material-icons">open_in_new</i>
                    </a>
                </div>
            </div>
        );
    }
}



