import * as moment from 'moment';
import {Countdown} from '../Components/CoundownTimer/CountdownTimer';

export default class CountdownUtil {
    public static generateCountdown(countdownFrom: moment.Moment, startTime: moment.Moment, eventDuration: number): Countdown {
        const now = countdownFrom;
        const endTime = startTime
            .clone()
            .add(eventDuration, 'minutes');
        const diffInSeconds = startTime.diff(now, 'seconds');

        let countdown: Countdown = {
            now,
            diffInSeconds,
            days: 0,
            hours: 0,
            minutes: 0,
            minutesString: '00',
            seconds: 0,
            secondsString: '00',
            complete: false,
            inProgress: false
        };
        if (startTime.isBefore(now) && endTime.isAfter(now)) { // event is in progress
            countdown.inProgress = true;
        } else if (endTime.isBefore(now)) { // event has finished
            countdown.complete = true;
        } else { // event has not happened yet
            const days : number = Math.floor(startTime.diff(now, 'hours') / 24);

            const hoursDiff = startTime.diff(now, 'hours');
            const hours = hoursDiff - (days * 24);

            const minutesDiff = startTime.diff(now, 'minutes');
            const minutes = minutesDiff - (days * 24 * 60) - (hours * 60);
            const minutesString : string = ('0' + minutes).substr(-2);

            const secondsDiff = startTime.diff(now, 'seconds');
            const seconds = secondsDiff - (days * 24 * 60 * 60) - (hours * 60 * 60) - (minutes * 60);
            const secondsString : string = ('0' + seconds).substr(-2);

            countdown = {
                ...countdown,
                days,
                hours,
                minutes,
                minutesString,
                seconds,
                secondsString
            };
        }
        return countdown;
    }
}



